import { NEW_POST, FETCH_POSTS } from '../actions/types';

const initialState = {
  items: [],
  item: {},
};

export default function(state = initialState, action) {
  switch (action.type) {
    case FETCH_POSTS:
      return {
        ...state,
        items: action.posts,
      };
    case NEW_POST: {
      return {
        ...state,
        item: action.post,
      };
    }

    default:
      return state;
  }
}



